/*begin hamburger*/
#nav-icon1 { width: 30px; height: 30px; position: relative; -webkit-transform: rotate(0deg); -moz-transform: rotate(0deg); -o-transform: rotate(0deg); transform: rotate(0deg); -webkit-transition: 0.5s ease-in-out; -moz-transition: 0.5s ease-in-out; -o-transition: 0.5s ease-in-out; transition: 0.5s ease-in-out; cursor: pointer; }

#nav-icon1 span { display: block; position: absolute; height: 3px; width: 100%; background: grey; border-radius: 9px; opacity: 1; left: 0; -webkit-transform: rotate(0deg); -moz-transform: rotate(0deg); -o-transform: rotate(0deg); transform: rotate(0deg); -webkit-transition: 0.25s ease-in-out; -moz-transition: 0.25s ease-in-out; -o-transition: 0.25s ease-in-out; transition: 0.25s ease-in-out; }

#nav-icon1 span:nth-child(1) { top: 0; }

#nav-icon1 span:nth-child(2) { top: 10px; }

#nav-icon1 span:nth-child(3) { top: 20px; }

#nav-icon1.open span:nth-child(1) { top: 15px; -webkit-transform: rotate(135deg); -moz-transform: rotate(135deg); -o-transform: rotate(135deg); transform: rotate(135deg); }

#nav-icon1.open span:nth-child(2) { opacity: 0; left: -60px; }

#nav-icon1.open span:nth-child(3) { top: 15px; -webkit-transform: rotate(-135deg); -moz-transform: rotate(-135deg); -o-transform: rotate(-135deg); transform: rotate(-135deg); }

