#calendar {
  margin-left: auto;
  margin-right: auto;
  max-width: 1370px;
}

#calendar__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 620px;
  margin: 0 auto;
  text-align: center;
  h4 {
    position: relative;
    width: 127px;
    height: 28px;
    font-family: $font-semibold;
    font-size: 26px;
    line-height: 1;
    color: #006eac;
    white-space: nowrap;
    @media only screen and (max-width: 480px) {
      width: auto;
    }
  }
  .month-next {
    position: absolute;
    right: -100%;
    top: 0px;
    bottom: 0;
    margin: auto;
    color: #b6bec3;
    @media only screen and (max-width: 480px) {
      display: none;
    }
  }
}

i[class^=ic-chevron] {
  .icon {
    fill: #006eac;
    font-size: 28px;
  }
  &:hover {
    cursor: pointer;
  }
}

#calendar__content,
#calendar__weekdays,
{
  position: relative;
  width: 100%;
}

#calendar__weekdays,
#calendar__content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  div {
    flex-basis: 14.2%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
}

#calendar__weekdays {
  margin-top: 20px;
  padding: 15px 0;
  font-family: $font-bold;
  font-size: 14px;
  line-height: 1;
  font-weight: bold;
  color: #a4a4a4;
}

#calendar__content div {
  height: 63px;
  text-align: center;
  background-color: #FFFFFF;
  color: #787878;
}

#calendar__content {
  border-left: solid 1px #d7d7d7;
  border-top: solid 1px #d7d7d7;
  div {
    font-family: $font-semibold;
    @include rfs(28);
    font-weight: 600;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: left;
    color: #013654;
    border-right: solid 1px #d7d7d7;
    border-bottom: solid 1px #d7d7d7;
    border-left: solid 1px transparent;
    border-top: solid 1px transparent;
  }
  .content__bottom {
    height: 14px;
    margin-top: 5px;
    font-family: $font-light;
    font-size: 16px;
    font-weight: 300;
    text-align: center;
    color: #838383;
  }
}

#calendar__content div:hover {
  color: #006eac;
  border: solid 1px $ocean-blue;
  cursor: pointer;
}

#calendar__content div.blank {
  background-color: #fafafa;
  color: #d8d8d8;
  @include rfs(20);
}

#calendar__content div.today {
  color: #FFFFFF;
}